html{
    scroll-behavior: smooth;
}
body{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    background: #2C2C2C;
    color:#D6D3D3;


}

.container{
    min-width: 320px;
    max-width:1440px;
}
h1, h2, h3, p{
    margin:0; 
}
h2{
    margin-bottom: 0.635rem;
}
p{
    margin-bottom: 0.3125rem;   
}
img{
    max-width: 100%;
    width: 100%;
    height:100%;
    vertical-align: middle;
    font-style: italic;
    background-repeat: no-repeat;
    background-size: cover;
    shape-margin: 0.75rem;
}
/*layout*/
.hero-section{
    height:60vh;
    
}

.button-section-wrapper{
    padding: 1rem ;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:0.625rem;
    
}
.button{
    padding: 0.5625rem 0.9375rem;
    border-radius: 50px;
    border: none;
    background: #D6D3D3;
    color:#707070;
   
    
}
.section-wrapper{
    padding: 1.875rem 6.25rem;
}
/*first template*/
.hero-section-wrapper{
    height: 100%;
    display: flex;
    position: relative;
}
.color-background{
    width: 50%;
    
    height: auto;
    border-radius: 0 30vh 30vh 0 ;
    background-color: rgba(57, 60, 65, 0.8);
    
    
    position: absolute;
            top: 0%;
            left: 0%;
            bottom: 0%;
            
           
            z-index: 2;
            display:flex;
            justify-content: center;
            align-items: center;
            text-align: center;
}
.img-wrapper{
    width: 100%;
    position: absolute;
    left:auto;
    top:0%;
    right: 0%;
    bottom:0%;
    
}
.element-label{
    font-size: 6.25rem;
}

/*MEDIA QUERIESSSSSSSSSSS*/
@media (max-width:768px){
    .hero-section{
        height:50vh;
        
    }
    .button-section-wrapper{
        padding: 0.5rem ;
        gap:0.3125rem;
        
    }
    .button{
        padding: 0.5625rem;
        border-radius: 50px;
        font-size: 0.75rem;
        
    }
    .section-wrapper{
        padding: 1.875rem 2.5rem;
    }


}